/* William Kimball 2018
This file styles the most components */

.dashboard-tripCards {
  -ms-flex-pack: distribute;
      justify-content: space-around;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}
.custCard{
  width: 90%
}

.card-cont {
  background: white;
}
.card.item {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  max-width: 40%;
  margin: 0 auto;
  margin-bottom: 3%;
}

.p-3 {
  padding: 0%;
  margin: 5%;
  margin-top: 2%;
  margin-bottom: 0%;
}

#newTripBtn,
#homeBtn {
  margin-right: 10%;
}

#homeBtn {
  margin-right: 28%;
}

#homeLogOut {
  -ms-flex-pack: distribute;
      justify-content: space-around;
}

dashButtons {
  -ms-flex-pack: justify;
      justify-content: space-between;
}

#loginLogo {
  max-width: 35%;
  max-height: 35%;
  margin: 0 auto;
  margin-top: 3%;
  margin-bottom: 3%;
}

div .columns.is-centered.is-grid.is-multiline.dashboard-tripCards {
  margin-bottom: 5%;
  margin-top: 2%;
}

button {
  overflow-wrap: break-word;
}
.budget-card {
  width: 60%;
  margin: 0 auto;
  text-align: center;
}
#budgetCard {
  background-color: #f3f3f3;
  margin-top: 5%;
  margin-bottom: 5%;
}
.budget-card-content {
  width: 60%;
  margin: 0 auto;
  max-height: 10%;
  background-color: #f3f3f3;
}

.itinerary-card-content {
  width: 80%;
  margin: 0 auto;
  margin-top: 1%;
  background-color: #ffffff;
  border: solid 2px #d5d5d5;
  border-radius: 4px;
}

.itineraryButton {
  max-width: 40%;
  -ms-flex-item-align: center;
      align-self: center;
  margin: 2%;
  padding: 3%
}

.button.is-info.itineraryButton{
  padding-left: 30%;
  padding-right: 30%
}

#budgetTotal {
  margin-top: 2%;
  margin-bottom: 2%;
}
.itinerary-card-body {
  max-height: 30px;
  margin-bottom: 2%;
}

#edtBtn {
  width: 6%;
  height: 6%;
  margin-left: 2%;
}

#deleteBtn {
  margin-left: 85%;
  width: 5%;
  height: 5%;
}
.modal-content.cardShrink {
  width: 80%;
  /* height: 200px; */
  margin-top: 2%;
  margin-bottom: 1%;
  /* display: block; */
}

div .cardShrink {
  position: relative;
  display: -ms-flexbox;
  /* display: flex; */
  -ms-flex-direction: column;
  flex-direction: column;
  width: 100%;
  pointer-events: auto;
  background-color: #fff;
  background-clip: padding-box;
  border: 1px solid rgba(0, 0, 0, 0.2);
  border-radius: 0.3rem;
  outline: 0;
  width: 80%;
  height: 200px;
  margin: 0 auto;
  margin-top: 2%;
  margin-bottom: 2%;
  text-align: center;
  padding-bottom: 2%;
  padding-top: 2%;
  height: 20%;
}

#deleteBtnCustList {
  display: -ms-flexbox;
  display: flex;
  width: 20%;
  height: 5%;
}

#listName {
  display: inline;
}

.card-body {
  min-width: 35%;
}
.customCard {
  width: 60%;
  margin: 0 auto;
  margin-top: 3%;
  height: 4em;
  background-color: #f3f3f3;
  -ms-flex-align: center;
      align-items: center;
}

#deleteBtnCust {
  margin-left: 80%;
  width: 25%;
  height: 25%;
  display: inline;
  margin-top: -2%;
}

figure.tripImage > img {
  -o-object-fit: cover;
     object-fit: cover;
}



